import cornerDotTypes from "../../constants/cornerDotTypes";
import { CornerDotType, RotateFigureArgs, BasicFigureDrawArgs, DrawArgs, Window } from "../../types";

export const availableCornerDotTypes = Object.values(cornerDotTypes);

export default class QRCornerDot {
  _element?: SVGElement;
  _svg: SVGElement;
  _type: CornerDotType;
  _window: Window;
  _customSvg?: string;

  constructor({
    svg,
    type,
    window,
    customSvg
  }: {
    svg: SVGElement;
    type: CornerDotType;
    window: Window;
    customSvg?: string;
  }) {
    this._svg = svg;
    this._type = type;
    this._window = window;
    this._customSvg = customSvg;
  }

  draw(x: number, y: number, size: number, rotation: number): void {
    //  SVG
    // if (this._customSvg) {
    //   this._drawCustomSvg({ x, y, size, rotation });
    //   return;
    // }
    const type = this._type;
    let drawFunction;

    switch (type) {
      case cornerDotTypes.square:
        drawFunction = this._drawSquare;
        break;
      case cornerDotTypes.star:
        drawFunction = this._drawStar;
        break;
      case cornerDotTypes.dot:
      default:
        drawFunction = this._drawDot;
    }

    drawFunction.call(this, { x, y, size, rotation });
  }

  _drawCustomSvg({ x, y, size, rotation }: DrawArgs): void {
    this._rotateFigure({
      x,
      y,
      size,
      rotation,
      draw: () => {
        this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "path");

        // 
        const cx = x + size / 2;
        const cy = y + size / 2;

        //  SVG 
        const origSize = 67;
        const origCenter = 33.5;

        // 
        const scale = size / origSize;

        // 
        const transformX = (origX: number) => cx + (origX - origCenter) * scale;
        const transformY = (origY: number) => cy + (origY - origCenter) * scale;

        // 
        const regex = /([MLHVCSQTAZ])\s*([-\d.,\s]+)/gi;
        let path = "";

        this._customSvg?.replace(regex, (match, command, coords) => {
          const numbers = coords
            .trim()
            .split(/[\s,]+/)
            .filter((n: string) => n);

          path += `${command} `;

          // 
          for (let i = 0; i < numbers.length; i += 2) {
            if (i > 0) path += " ";
            const x = transformX(numbers[i]);
            const y = transformY(numbers[i + 1]);
            path += `${x} ${y}`;
          }

          return match;
        });

        this._element.setAttribute("d", path);
      }
    });
  }

  _rotateFigure({ x, y, size, rotation = 0, draw }: RotateFigureArgs): void {
    const cx = x + size / 2;
    const cy = y + size / 2;

    draw();
    this._element?.setAttribute("transform", `rotate(${(180 * rotation) / Math.PI},${cx},${cy})`);
  }

  _basicDot(args: BasicFigureDrawArgs): void {
    const { size, x, y } = args;

    this._rotateFigure({
      ...args,
      draw: () => {
        this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "circle");
        this._element.setAttribute("cx", String(x + size / 2));
        this._element.setAttribute("cy", String(y + size / 2));
        this._element.setAttribute("r", String(size / 2));
      }
    });
  }

  _basicSquare(args: BasicFigureDrawArgs): void {
    const { size, x, y } = args;

    this._rotateFigure({
      ...args,
      draw: () => {
        this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "rect");
        this._element.setAttribute("x", String(x));
        this._element.setAttribute("y", String(y));
        this._element.setAttribute("width", String(size));
        this._element.setAttribute("height", String(size));
      }
    });
  }

  _basicStar(args: BasicFigureDrawArgs): void {
    const { size, x, y } = args;

    this._rotateFigure({
      ...args,
      draw: () => {
        this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "path");

        // 
        const cx = x + size / 2;
        const cy = y + size / 2;

        //  SVG 
        const origSize = 67;
        const origCenter = 33.5;

        // 
        const scale = size / origSize;

        // 
        const transformX = (origX: number) => cx + (origX - origCenter) * scale;
        const transformY = (origY: number) => cy + (origY - origCenter) * scale;

        //  star.svg 
        const path =
          `M ${transformX(33.5)} ${transformY(4)}` +
          `C ${transformX(33.5)} ${transformY(4)} ` +
          `${transformX(37.9276)} ${transformY(17.068)} ` +
          `${transformX(43.9298)} ${transformY(23.0702)}` +
          `C ${transformX(49.932)} ${transformY(29.0724)} ` +
          `${transformX(63)} ${transformY(33.5)} ` +
          `${transformX(63)} ${transformY(33.5)}` +
          `C ${transformX(63)} ${transformY(33.5)} ` +
          `${transformX(49.932)} ${transformY(37.9276)} ` +
          `${transformX(43.9298)} ${transformY(43.9298)}` +
          `C ${transformX(37.9276)} ${transformY(49.932)} ` +
          `${transformX(33.5)} ${transformY(63)} ` +
          `${transformX(33.5)} ${transformY(63)}` +
          `C ${transformX(33.5)} ${transformY(63)} ` +
          `${transformX(29.0724)} ${transformY(49.932)} ` +
          `${transformX(23.0702)} ${transformY(43.9298)}` +
          `C ${transformX(17.068)} ${transformY(37.9276)} ` +
          `${transformX(4)} ${transformY(33.5)} ` +
          `${transformX(4)} ${transformY(33.5)}` +
          `C ${transformX(4)} ${transformY(33.5)} ` +
          `${transformX(17.068)} ${transformY(29.0724)} ` +
          `${transformX(23.0702)} ${transformY(23.0702)}` +
          `C ${transformX(29.0724)} ${transformY(17.068)} ` +
          `${transformX(33.5)} ${transformY(4)} ` +
          `${transformX(33.5)} ${transformY(4)}` +
          `Z`;

        this._element.setAttribute("d", path);
      }
    });
  }

  _drawDot({ x, y, size, rotation }: DrawArgs): void {
    this._basicDot({ x, y, size, rotation });
  }

  _drawSquare({ x, y, size, rotation }: DrawArgs): void {
    this._basicSquare({ x, y, size, rotation });
  }

  _drawStar({ x, y, size, rotation }: DrawArgs): void {
    this._basicStar({ x, y, size, rotation });
  }
}
