    console.log(rotation);

    //  SVG 
    // this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "path");
    //   //  d 
    //   const pathData = this._customSvg[0];
    //   this._element.setAttribute("d", pathData);
    //   this._element.setAttribute("fill", "none");
    //   this._element.setAttribute("stroke-width", "16");
    //   //  SVG  viewBox  159x165 8.svg
    //   //  size
    //   const scale = size / 165;
    //   // const cx = x + size / 2;
    //   // const cy = y + size / 2;

    //   //  transform
    //   let transform = `translate(${x}, ${y}) scale(${scale})`;
    //   if (rotation !== 0) {
