import { TypeNumber, ErrorCorrectionLevel } from "../types";

/**
 *  QR Total Number of Codewords
 * @param typeNumber -  (1-40)
 * @param errorCorrectionLevel -  ('L', 'M', 'Q', 'H')
 * @returns 
 */
