import dotTypes from "../../constants/dotTypes";
import { DotType, GetNeighbor, DrawArgs, BasicFigureDrawArgs, RotateFigureArgs, Window } from "../../types";

export default class QRDot {
  _element?: SVGElement;
  _svg: SVGElement;
  _type: DotType;
  _window: Window;
  _drawnRectangles: Set<string>; //  stripe 

  constructor({ svg, type, window }: { svg: SVGElement; type: DotType; window: Window }) {
    this._svg = svg;
    this._type = type;
    this._window = window;
    this._drawnRectangles = new Set();
  }

  draw(x: number, y: number, size: number, getNeighbor: GetNeighbor): void {
    const type = this._type;
    let drawFunction;

    switch (type) {
      case dotTypes.dots:
        drawFunction = this._drawDot;
        break;
      case dotTypes.classy:
        drawFunction = this._drawClassy;
        break;
      case dotTypes.classyRounded:
        drawFunction = this._drawClassyRounded;
        break;
      case dotTypes.rounded:
        drawFunction = this._drawRounded;
        break;
      case dotTypes.extraRounded:
        drawFunction = this._drawExtraRounded;
        break;
      case dotTypes.star:
        drawFunction = this._drawStar;
        break;
      case dotTypes.horBar:
        drawFunction = this._drawHorBar;
        break;
      case dotTypes.verBar:
        drawFunction = this._drawVerBar;
        break;
      case dotTypes.square:
      default:
        drawFunction = this._drawSquare;
    }

    drawFunction.call(this, { x, y, size, getNeighbor, type });
  }

  //  stripe 
  clearDrawnRectangles(): void {
    this._drawnRectangles.clear();
  }

  _rotateFigure({ x, y, size, rotation = 0, draw }: RotateFigureArgs): void {
    const cx = x + size / 2;
    const cy = y + size / 2;

    draw();
    this._element?.setAttribute("transform", `rotate(${(180 * rotation) / Math.PI},${cx},${cy})`);
  }

  _basicDot(args: BasicFigureDrawArgs): void {
    const { size, x, y } = args;

    this._rotateFigure({
      ...args,
      draw: () => {
        this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "circle");
        this._element.setAttribute("cx", String(x + size / 2));
        this._element.setAttribute("cy", String(y + size / 2));
        this._element.setAttribute("r", String(size / 2.3)); // 
      }
    });
  }

  _basicSquare(args: BasicFigureDrawArgs): void {
    const { size, x, y, type } = args;

    console.log(type);

    // TODO: 
    const cornerRadius = type === dotTypes.square ? size * 0.2 : size * 0;
    //  85% 
    const squareSize = type === dotTypes.square ? size * 0.85 : size * 1;
    // 
    const offset = (size - squareSize) / 2;

    this._rotateFigure({
      ...args,
      draw: () => {
        // this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "rect");
        // this._element.setAttribute("x", String(x));
        // this._element.setAttribute("y", String(y));
        // this._element.setAttribute("width", String(size));
        // this._element.setAttribute("height", String(size));
        // this._element.setAttribute("rx", String(cornerRadius));
        // this._element.setAttribute("ry", String(cornerRadius));

        this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "rect");
        this._element.setAttribute("x", String(x + offset));
        this._element.setAttribute("y", String(y + offset));
        this._element.setAttribute("width", String(squareSize));
        this._element.setAttribute("height", String(squareSize));
        this._element.setAttribute("rx", String(cornerRadius));
        this._element.setAttribute("ry", String(cornerRadius));
      }
    });
  }

  //if rotation === 0 - right side is rounded
  _basicSideRounded(args: BasicFigureDrawArgs): void {
    const { size, x, y } = args;

    this._rotateFigure({
      ...args,
      draw: () => {
        this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "path");
        this._element.setAttribute(
          "d",
          `M ${x} ${y}` + //go to top left position
            `v ${size}` + //draw line to left bottom corner
            `h ${size / 2}` + //draw line to left bottom corner + half of size right
            `a ${size / 2} ${size / 2}, 0, 0, 0, 0 ${-size}` // draw rounded corner
        );
      }
    });
  }

  //if rotation === 0 - top right corner is rounded
  _basicCornerRounded(args: BasicFigureDrawArgs): void {
    const { size, x, y } = args;

    this._rotateFigure({
      ...args,
      draw: () => {
        this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "path");
        this._element.setAttribute(
          "d",
          `M ${x} ${y}` + //go to top left position
            `v ${size}` + //draw line to left bottom corner
            `h ${size}` + //draw line to right bottom corner
            `v ${-size / 2}` + //draw line to right bottom corner + half of size top
            `a ${size / 2} ${size / 2}, 0, 0, 0, ${-size / 2} ${-size / 2}` // draw rounded corner
        );
      }
    });
  }

  //if rotation === 0 - top right corner is rounded
  _basicCornerExtraRounded(args: BasicFigureDrawArgs): void {
    const { size, x, y } = args;

    this._rotateFigure({
      ...args,
      draw: () => {
        this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "path");
        this._element.setAttribute(
          "d",
          `M ${x} ${y}` + //go to top left position
            `v ${size}` + //draw line to left bottom corner
            `h ${size}` + //draw line to right bottom corner
            `a ${size} ${size}, 0, 0, 0, ${-size} ${-size}` // draw rounded top right corner
        );
      }
    });
  }

  //if rotation === 0 - left bottom and right top corners are rounded
  _basicCornersRounded(args: BasicFigureDrawArgs): void {
    const { size, x, y } = args;

    this._rotateFigure({
      ...args,
      draw: () => {
        this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "path");
        this._element.setAttribute(
          "d",
          `M ${x} ${y}` + //go to left top position
            `v ${size / 2}` + //draw line to left top corner + half of size bottom
            `a ${size / 2} ${size / 2}, 0, 0, 0, ${size / 2} ${size / 2}` + // draw rounded left bottom corner
            `h ${size / 2}` + //draw line to right bottom corner
            `v ${-size / 2}` + //draw line to right bottom corner + half of size top
            `a ${size / 2} ${size / 2}, 0, 0, 0, ${-size / 2} ${-size / 2}` // draw rounded right top corner
        );
      }
    });
  }

  _drawDot({ x, y, size }: DrawArgs): void {
    this._basicDot({ x, y, size, rotation: 0 });
  }

  _drawSquare({ x, y, size, type }: DrawArgs): void {
    this._basicSquare({ x, y, size, rotation: 0, type });
  }

  _drawRounded({ x, y, size, getNeighbor }: DrawArgs): void {
    const leftNeighbor = getNeighbor ? +getNeighbor(-1, 0) : 0;
    const rightNeighbor = getNeighbor ? +getNeighbor(1, 0) : 0;
    const topNeighbor = getNeighbor ? +getNeighbor(0, -1) : 0;
    const bottomNeighbor = getNeighbor ? +getNeighbor(0, 1) : 0;

    const neighborsCount = leftNeighbor + rightNeighbor + topNeighbor + bottomNeighbor;

    if (neighborsCount === 0) {
      this._basicDot({ x, y, size, rotation: 0 });
      return;
    }

    if (neighborsCount > 2 || (leftNeighbor && rightNeighbor) || (topNeighbor && bottomNeighbor)) {
      this._basicSquare({ x, y, size, rotation: 0 });
      return;
    }

    if (neighborsCount === 2) {
      let rotation = 0;

      if (leftNeighbor && topNeighbor) {
        rotation = Math.PI / 2;
      } else if (topNeighbor && rightNeighbor) {
        rotation = Math.PI;
      } else if (rightNeighbor && bottomNeighbor) {
        rotation = -Math.PI / 2;
      }

      this._basicCornerRounded({ x, y, size, rotation });
      return;
    }

    if (neighborsCount === 1) {
      let rotation = 0;

      if (topNeighbor) {
        rotation = Math.PI / 2;
      } else if (rightNeighbor) {
        rotation = Math.PI;
      } else if (bottomNeighbor) {
        rotation = -Math.PI / 2;
      }

      this._basicSideRounded({ x, y, size, rotation });
      return;
    }
  }

  _drawExtraRounded({ x, y, size, getNeighbor }: DrawArgs): void {
    const leftNeighbor = getNeighbor ? +getNeighbor(-1, 0) : 0;
    const rightNeighbor = getNeighbor ? +getNeighbor(1, 0) : 0;
    const topNeighbor = getNeighbor ? +getNeighbor(0, -1) : 0;
    const bottomNeighbor = getNeighbor ? +getNeighbor(0, 1) : 0;

    const neighborsCount = leftNeighbor + rightNeighbor + topNeighbor + bottomNeighbor;

    if (neighborsCount === 0) {
      this._basicDot({ x, y, size, rotation: 0 });
      return;
    }

    if (neighborsCount > 2 || (leftNeighbor && rightNeighbor) || (topNeighbor && bottomNeighbor)) {
      this._basicSquare({ x, y, size, rotation: 0 });
      return;
    }

    if (neighborsCount === 2) {
      let rotation = 0;

      if (leftNeighbor && topNeighbor) {
        rotation = Math.PI / 2;
      } else if (topNeighbor && rightNeighbor) {
        rotation = Math.PI;
      } else if (rightNeighbor && bottomNeighbor) {
        rotation = -Math.PI / 2;
      }

      this._basicCornerExtraRounded({ x, y, size, rotation });
      return;
    }

    if (neighborsCount === 1) {
      let rotation = 0;

      if (topNeighbor) {
        rotation = Math.PI / 2;
      } else if (rightNeighbor) {
        rotation = Math.PI;
      } else if (bottomNeighbor) {
        rotation = -Math.PI / 2;
      }

      this._basicSideRounded({ x, y, size, rotation });
      return;
    }
  }

  _drawClassy({ x, y, size, getNeighbor }: DrawArgs): void {
    const leftNeighbor = getNeighbor ? +getNeighbor(-1, 0) : 0;
    const rightNeighbor = getNeighbor ? +getNeighbor(1, 0) : 0;
    const topNeighbor = getNeighbor ? +getNeighbor(0, -1) : 0;
    const bottomNeighbor = getNeighbor ? +getNeighbor(0, 1) : 0;

    const neighborsCount = leftNeighbor + rightNeighbor + topNeighbor + bottomNeighbor;

    if (neighborsCount === 0) {
      this._basicCornersRounded({ x, y, size, rotation: Math.PI / 2 });
      return;
    }

    if (!leftNeighbor && !topNeighbor) {
      this._basicCornerRounded({ x, y, size, rotation: -Math.PI / 2 });
      return;
    }

    if (!rightNeighbor && !bottomNeighbor) {
      this._basicCornerRounded({ x, y, size, rotation: Math.PI / 2 });
      return;
    }

    this._basicSquare({ x, y, size, rotation: 0 });
  }

  _drawClassyRounded({ x, y, size, getNeighbor }: DrawArgs): void {
    const leftNeighbor = getNeighbor ? +getNeighbor(-1, 0) : 0;
    const rightNeighbor = getNeighbor ? +getNeighbor(1, 0) : 0;
    const topNeighbor = getNeighbor ? +getNeighbor(0, -1) : 0;
    const bottomNeighbor = getNeighbor ? +getNeighbor(0, 1) : 0;

    const neighborsCount = leftNeighbor + rightNeighbor + topNeighbor + bottomNeighbor;

    if (neighborsCount === 0) {
      this._basicCornersRounded({ x, y, size, rotation: Math.PI / 2 });
      return;
    }

    if (!leftNeighbor && !topNeighbor) {
      this._basicCornerExtraRounded({ x, y, size, rotation: -Math.PI / 2 });
      return;
    }

    if (!rightNeighbor && !bottomNeighbor) {
      this._basicCornerExtraRounded({ x, y, size, rotation: Math.PI / 2 });
      return;
    }

    this._basicSquare({ x, y, size, rotation: 0 });
  }

  // 
  // _drawStar({ x, y, size }: DrawArgs): void {
  //   //  star.svg 
  //   //  SVG viewBox  "0 0 67 67"
  //   const pathData =
  //     "M33.5 4C33.5 4 37.9276 17.068 43.9298 23.0702C49.932 29.0724 63 33.5 63 33.5C63 33.5 49.932 37.9276 43.9298 43.9298C37.9276 49.932 33.5 63 33.5 63C33.5 63 29.0724 49.932 23.0702 43.9298C17.068 37.9276 4 33.5 4 33.5C4 33.5 17.068 29.0724 23.0702 23.0702C29.0724 17.068 33.5 4 33.5 4Z";

  //   this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "path");
  //   this._element.setAttribute("d", pathData);

  //   //  67x67  SVG  size
  //   const scale = size / 67;
  //   this._element.setAttribute("transform", `translate(${x}, ${y}) scale(${scale})`);
  // }

  // 
  _drawStar({ x, y, size }: DrawArgs): void {
    // 4
    //  67x67 4
    const pathData = "M33.5 4 L63 33.5 L33.5 63 L4 33.5 Z";

    this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "path");
    this._element.setAttribute("d", pathData);

    //  67x67  SVG  size
    const scale = size / 67;
    this._element.setAttribute("transform", `translate(${x}, ${y}) scale(${scale})`);
  }

  _drawBar({ x, y, size, getNeighbor }: DrawArgs, type: DotType): void {
    if (!getNeighbor) {
      this._basicDot({ x, y, size, rotation: 0 });
      return;
    }

    // 
    const pointKey = `${x},${y}`;

    // 
    if (this._drawnRectangles.has(pointKey)) {
      return;
    }

    const rectangleSizes =
      type === dotTypes.horBar
        ? [
            [3, 1],
            [2, 1],
            [1, 1]
          ]
        : [
            [1, 3],
            [1, 2],
            [1, 1]
          ];

    //  true
    const checkRectangle = (startX: number, startY: number, width: number, height: number): boolean => {
      for (let dx = 0; dx < width; dx++) {
        for (let dy = 0; dy < height; dy++) {
          if (!getNeighbor(startX + dx, startY + dy)) {
            return false;
          }
        }
      }
      return true;
    };

    // 
    const findRectangleForCurrentPoint = (): {
      width: number;
      height: number;
    } | null => {
      // 
      for (const [width, height] of rectangleSizes) {
        //  true
        if (checkRectangle(0, 0, width, height)) {
          return { width, height };
        }
      }
      return null;
    };

    const rectangle = findRectangleForCurrentPoint();

    // 
    if (!rectangle) {
      this._basicDot({ x, y, size, rotation: 0 });
      return;
    }

    const { width, height } = rectangle;

    // 
    for (let dx = 0; dx < width; dx++) {
      for (let dy = 0; dy < height; dy++) {
        const coveredX = x + dx * size;
        const coveredY = y + dy * size;
        this._drawnRectangles.add(`${coveredX},${coveredY}`);
      }
    }

    // 
    const baseRectX = x;
    const baseRectY = y;
    const baseRectWidth = width * size;
    const baseRectHeight = height * size;

    // 
    const shrinkFactor = 0.85; //  85%0.8-0.9
    const rectWidth = baseRectWidth * shrinkFactor;
    const rectHeight = baseRectHeight * shrinkFactor;
    const rectX = baseRectX + (baseRectWidth - rectWidth) / 2;
    const rectY = baseRectY + (baseRectHeight - rectHeight) / 2;

    // 
    const cornerRadius = 7; //  25% 

    //  SVG 
    this._element = this._window.document.createElementNS("http://www.w3.org/2000/svg", "rect");
    this._element.setAttribute("x", String(rectX));
    this._element.setAttribute("y", String(rectY));
    this._element.setAttribute("width", String(rectWidth));
    this._element.setAttribute("height", String(rectHeight));
    this._element.setAttribute("rx", String(cornerRadius));
    this._element.setAttribute("ry", String(cornerRadius));
  }

  _drawHorBar({ x, y, size, getNeighbor }: DrawArgs): void {
    this._drawBar({ x, y, size, getNeighbor }, dotTypes.horBar);
  }

  _drawVerBar({ x, y, size, getNeighbor }: DrawArgs): void {
    this._drawBar({ x, y, size, getNeighbor }, dotTypes.verBar);
  }
}
